const state = {
    isOpen: false
};
const getters = {
    getOpenStatus : state => state.isOpen
};
const mutations = {
    TOGGLE_MENU(state) {
        (!state.isOpen) ? state.isOpen = true : state.isOpen = false
    }
};
const actions = {
    toggleMenu({commit}) {
        commit("TOGGLE_MENU")
    }
};

export default {
    state,
    getters,
    mutations,
    actions
}