import axios from "axios";

const state = {
    todos : []
};
const getters = {
    allTodos : state => state.todos
};
const mutations = {
    update_data(state, data) {
        state.todos = data
    },
    post_data(state, todo) {
        state.todos.unshift(todo)
    },
    remove_data(state, id) {
        state.todos.splice(id, 1);
    }
};
const actions = {
    getTodos({commit}) {
        axios.get("https://jsonplaceholder.typicode.com/todos")
        .then(res => commit("update_data", res.data))
        .catch(err => console.log(err))
    },
    postTodo({commit}, title ) {
        axios.post("https://jsonplaceholder.typicode.com/todos", { title , completed : false })
        .then( res => commit('post_data', res.data))
        .catch(err=> console.log(err))
    },
    delTodo({commit}, id) {
        axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
        .then( res => commit('remove_data', res.data))
        .catch(err => console.log(err))
    },
    filterTodos({commit}, e) {
        let limit = e.target.options[e.target.options.selectedIndex].innerText;
        axios.get(`https://jsonplaceholder.typicode.com/todos/?_limit=${limit}`)
        .then( res => commit('update_data', res.data))
        .catch(err => console.log(err))
    }
};

export default {
    state,
    getters,
    mutations,
    actions
}