<template>
  <div class="sidemenu" :class="{isOpen : getOpenStatus }">
      <div v-for="(category, index) in categories" :key="index" :class="{hidden : !isOpen }">
          <p>{{ category }} </p>
      </div>
      <!-- <ul>
        <li v-for="(icon, index) in menuIcons" :key="index" :class="{hidden : isOpen }">
            <font-awesome-icon :icon="icon" class="menuIcon m-2"/>
        </li>
      </ul> -->
  </div>
</template>

<script>
import { mapGetters } from "vuex"
export default {
    name: "SideMenu",
    data() {
        return {
            isOpen: mapGetters(["getOpenStatus"]),
            menuIcons: ["home", "cog", "users", "sign-out-alt"],
            categories : ['Home', 'Profile', 'Services', 'Projects', 'Logout']
        }
    },
    computed: mapGetters(["getOpenStatus"])

}
</script>

<style scoped>
.sidemenu {
     width: 200px;
    position: fixed;
    top:56px;
    left: 0;
    bottom: 0;
    background: #333;
    color: #fff;
    transition: transform 0.6s ease;
    transform: translateX(-100%);
}

.sidemenu.isOpen{
    transform: translateX(0);
}
.sidemenu.isOpen .hidden {
    visibility: hidden;
}

.sidemenu p{
    text-align: center;
    padding: 5px 8px;
    transition: all 0.6s ease-in-out;
}
.sidemenu p:hover{
    background: rgb(5, 5, 5);
    cursor: pointer;
}
.menuIcon {
    position: absolute;
    right: 10px;
    font-size: 1.5rem;
}

.sidemenu ul {
    background: red;
    padding: 10px 0;
    min-height: 300px;
}
.sidemenu ul li {
    padding: 1.5rem;
    list-style-type: none;
}
</style>